<template>
  <div>
    <h1 class="mt-3">Mitarbeiterliste</h1>
    <table class="table">
      <thead>
        <tr>
          <th>ID</th>
          <th>Vorname</th>
          <th>Nachname</th>
          <th>AHV</th>
          <th>Adresse</th>
          <th>Telefonnummer</th>
          <th>Team</th>
        </tr>
      </thead>
      <tbody>
         <tr :key="index" v-for="(index,i) in 100">
            
          <td>
            {{ persons[i].id }}
          </td> 
          <td>
            {{ persons[i].firstname }}
          </td>
          <td>
            {{ persons[i].lastname }}
          </td>
          <td>
            {{ persons[i].ahv }}
          </td>
          <td>
            {{ persons[i].adress }}
          </td>
          <td>
            {{ persons[i].number }}
          </td>
          <td>
            {{teams[i].description}}
          </td>
         </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data: function () {
    return {
      persons: [], 
      teams: [],
    };
  },
  mounted: function() {
      this.getPersons();
      this.getTeams();
  },
  methods: {
      getPersons: function() {
          let url = "http://localhost:8080/shiftplanner/persons"
          axios.get(url).then(response => {
              this.persons = response.data;
          })
      },
      getTeams: function(){
         let url = "http://localhost:8080/shiftplanner/teams"
          axios.get(url).then(response => {
              this.teams = response.data;
          })
      },
  }
};
</script>



 